@import url(https://fonts.googleapis.com/css2?family=Lora&family=Nunito:wght@300;400;700&display=swap);
header{
  display: grid;
  padding: 10px 2vw;
  max-height: 4vh;
  background-color: #3b2e5a;
}
.logoNav{
  grid-area: logo;
}

.hide {
  display: none;
  width: 15px;
  height: 15px;
}
.userName{
  grid-area: username;
  display: flex;
  justify-content: flex-end;
}

.burger{
  grid-area: burger;
  margin-left: 12px;
  margin-top: 11px;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}
.buttons a, .userName a {
  color:white;
  margin-top: 7px;

  display: flex;
}
.userName a{
  margin-top: 6px;
}
.buttons a::after{
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  background-color: #111; 
  -webkit-transition: all 0.1s ease; 
  transition: all 0.1s ease;
}

.buttons{

  display: flex;
  grid-area: nav;
}
header a{
  text-decoration: none;
  color: white;
}
header a h3 {
  text-decoration: none;
}
.buttons a img.imgButton {
  display: inline-block;
  width: 15px;
  height: 15px;
  margin-right: 5px;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}
.showRoute{
  padding: 10px 2vw;
  max-height: 3vh;
  display: flex;
  color: wheat;
  background-color: #574f82;
  
}
.showRoute a{
  text-decoration: none;
  display: inline;
  color: wheat;
  font-weight: 600;
}
.showRoute p {
  font-family: Lora;
}
@media only screen and (max-width: 48em) {
  header{
    max-height: 200px;
    grid-template-areas: 
    "logo  .  username  burger "
    ". nav nav ."
  }
  .buttons{
    max-height: 0;
    transition: max-height 1s ease-in-out, -webkit-transform 1s  ease;
    overflow: hidden;
  }
  .buttons.show{
    max-height: 200px;
    justify-content: center;
  }
  .buttons.show{
    max-height: 200px;
    justify-content: center;
  }
  .hide {
    display: block;
    
    transition: all 0.7s ease, -webkit-transform 0.7s ease;
  }

  .hide.twist {
    margin-top: 1px ;
    width: 16px;
    height: 16px;
    margin-top: 0px;
    transition: all 0.7s ease, -webkit-transform 0.7s ease;
  }

  .buttons a {
    padding:10px;
    border-radius: 8px;
    background-color: rgba(0,0,0,0.2);
    margin-right: 2vw;
  }
  .userName{
    margin-right: 15px;
  }
  .burger {
    height: 15px;
  }
  .userName a{
    margin-top: 8px;
  }
}
@media only screen and (min-width: 48em) {
 header{
    display: flex;
 }
 .burger{
   flex: 1 1;
 }
 .buttons a {
   background-color: transparent;
   font-size: .9em;  
 }

 .buttons a {
  margin-left: 3vw;
}
.buttons{
  justify-content: flex-end;
}
