/*estilos se comparten entre regiter, login y new task*/
.loginContainer {
  min-height: 87vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #d9d9d9;
}
.containerf {
  height: 380px;
  width: 320px;
  background-color: white;
}
.containerf.task {
  width: 340px;
  max-height: 600px;
  height: auto;
  background: white;
  display: flex;
  position: relative;
}
.containerf form {
  width: 95%;
  padding: 10px;
}
form h3 {
  text-align: center;
  margin: 10px;
}
form .field {
  margin: 20px 0;
}
form .field label {
  font-size: 0.8em;
  font-weight: bold;
  margin: 20px 0;
}
form .field input, form .field textarea  {
  width: 97%;
  border-style: none;
  height: 25px;
  padding: 5px;
  margin: 5px 0;
  background-color: rgb(243, 242, 242);
  border-bottom: 2px solid rgba(28, 110, 164, 0.13);
  border-radius: 3px;
  font-size: .9em;
}

form .field textarea {
  resize: none;
  height: 100px;
}
form .field input, form .field textarea{
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
form .field input[type="text"]:focus, form .field textarea:focus, input[type="password"]:focus{
  background-color: rgba(0,0,0,.4);
  
}
form .field .error {
  font-size: 0.7em;
  color: #e81123;
}
.resMessage {
  text-align: center;
  font-size: 0.7em;
  margin-top: 15px;
}
form button.send {
  width: 100%;
  height: 60px;
  background-color: rgb(179, 178, 178);
  color: #334049;
  font-size: 1.2em;
  cursor: pointer;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;

  -webkit-transition: all .3s ease-in-out;

  transition: all .3s ease-in-out;
}
form button:hover {
  background-color: rgba(0,0,0,.7);
  color: white;
}
@media only screen and (max-width: 48em) {
  .loginContainer {
    min-height: 87.5vh;
  }
  .containerf {
    width: 95%;
  }
}

